@use "global" as *;

.page-information-card {
  border: 1px solid $main;
}

.page-information-card__inner {
  padding: rem(24);
  @include mq('md') {
    display: flex;
    align-items: center;
    gap: rem(48);
  }
}

.page-information-card__head {
  @include mq('md') {
    width: 50%;
  }
}

.page-information-card__title {
  font-size: rem(24);
  font-family: $base-font-family;
  font-weight: $bold;
  line-height: 1;
  border-bottom: 1px solid $main;
  padding-bottom: rem(8);
}

.page-information-card__text {
  margin-top: rem(8);
  font-size: rem(16);
  font-family: $base-font-family;
  font-weight: $normal;
  line-height: 1.5;
}

.page-information-card__image {
  margin-top: rem(8);
  position: relative;
  @include mq('md') {
    margin-top: 0;
    width: 50%;
  }
}

.page-information-card__image::before {
  content: "";
  display: block;
  padding-top: 63.63%;
  @include mq('md') {
    padding-top: 63.61%;
  }
}

.page-information-card__image img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50% , -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
}