[type=checkbox] {
  display: none;
}
[type=checkbox] + span {
  display: inline-block;
  position: relative;
  padding: 0 0 0 1.375rem;
  margin: 0 1.25rem 0 0;
  font-size: 1rem;
  line-height: 1;
  cursor: pointer;
  transition: 0.3s ease;
}
[type=checkbox] + span::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  background: #fff;
  border: 1px solid #004c42;
  width: 0.875rem;
  height: 0.875rem;
  display: block;
}
[type=checkbox] + span::after {
  opacity: 0;
  transition: 0.3s ease;
}
[type=checkbox]:checked + span::after {
  content: "";
  position: absolute;
  top: 52%;
  left: 0.0625rem;
  display: block;
  opacity: 1;
  /*チェックマークじゃない場合は以下を変更する*/
  transform: translateY(-50%) rotate(-45deg);
  width: 0.56rem;
  height: 0.315rem;
  margin-top: -0.1em;
  margin-left: 0.1em;
  border-left: 2px solid #C94800;
  border-bottom: 2px solid #C94800;
}

[type=radio] {
  display: none;
}
[type=radio] + span {
  display: inline-block;
  position: relative;
  padding: 0 0 0 1.75rem;
  margin: 0 2.1875rem 0 0;
  font-size: 0.75rem;
  line-height: 1;
  cursor: pointer;
}
[type=radio] + span::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background: #fff;
  border: 1px solid #707070;
  border-radius: 50%;
  width: 1.125rem;
  height: 1.125rem;
  display: block;
}
[type=radio] + span::after {
  opacity: 0;
  transition: 0.3s ease;
}
[type=radio]:checked + span::after {
  position: absolute;
  content: "";
  top: 50%;
  left: 0.1875rem;
  transform: translateY(-50%);
  background: #004c42;
  border-radius: 50%;
  width: 0.75rem;
  height: 0.75rem;
  display: block;
  opacity: 1;
}

