body {
  position: relative;
}

.to-top {
  position: fixed;
  right: 0.9375rem;
  bottom: 1rem;
  overflow: hidden;
  border-radius: 50%;
  z-index: 100;
}
@media screen and (min-width: 768px) {
  .to-top {
    right: 1.25rem;
  }
}

.to-top a {
  display: block;
  width: 3rem;
  height: 3rem;
  background-color: #FFFFFF;
  border: 1px solid #004c42;
  border-radius: 50%;
  transform: rotate(-90deg);
  position: relative;
}
@media screen and (min-width: 768px) {
  .to-top a {
    width: 5rem;
    height: 5rem;
  }
}

.to-top a span {
  position: absolute;
  width: 1.75rem;
  height: 0.375rem;
  border-bottom: 1px solid #004c42;
  border-right: 1px solid #004c42;
  top: 48%;
  left: 45%;
  transform: translate(-50%, -50%) skew(45deg);
}
@media screen and (min-width: 768px) {
  .to-top a span {
    width: 2.5rem;
    height: 0.5rem;
  }
}

.is-hidden {
