.page.page--pc {
  display: none;
}

.previouspostslink {
  margin-right: 1.75rem;
  display: block;
  width: 2.375rem;
  height: 2.375rem;
  border: 1px solid #004c42;
  background-color: #FFFFFF;
  transition: all 0.3s;
  position: relative;
}

.previouspostslink:hover {
  cursor: pointer;
  background-color: #004c42;
}

.previouspostslink::after {
  content: "";
  position: absolute;
  top: 34%;
  left: 42%;
  transform: translate(-50%, -50%);
  display: block;
  width: 0.75rem;
  height: 0.75rem;
  border-left: 1px solid #004c42;
  border-bottom: 1px solid #004c42;
  transform: rotate(45deg);
  transition: all 0.3s;
}

.previouspostslink:hover::after {
  border-left: 1px solid #FFFFFF;
  border-bottom: 1px solid #FFFFFF;
}

.nextpostslink {
  margin-left: 1.75rem;
  display: block;
  width: 2.375rem;
  height: 2.375rem;
  border: 1px solid #004c42;
  background-color: #FFFFFF;
  transition: all 0.3s;
  position: relative;
}

.nextpostslink:hover {
  cursor: pointer;
  background-color: #004c42;
}

.nextpostslink::after {
  content: "";
  position: absolute;
  top: 34%;
  left: 24%;
  transform: translate(-50%, -50%);
  display: block;
  width: 0.75rem;
  height: 0.75rem;
  border-right: 1px solid #004c42;
  border-top: 1px solid #004c42;
  transform: rotate(45deg);
  transition: all 0.3s;
}

.nextpostslink:hover::after {
  border-right: 1px solid #FFFFFF;
  border-top: 1px solid #FFFFFF;
}