@use "global" as *;

.wp-pagenavi {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: center;
  gap: rem(12);
  justify-content: center;
  width: 100%;
  @include mq('md') {
    max-width: rem(444);
  }
}

.wp-pagenavi a,.wp-pagenavi span {
  display: inline-block;
  padding-top: rem(10);
  padding-bottom: rem(10);
  width: rem(38);
  color: $main;
  background-color: $base;
  border: 1px solid $main;
  font-family: $third-font-family;
  font-size: rem(16);
  font-weight: $bold;
  line-height: 1;
  text-align: center;
  transition: .3s;
}

.wp-pagenavi a:hover {
  color: $base;
  background-color: $main;
  cursor: pointer;
}

.wp-pagenavi span.current {
  color: $base;
  background-color: $main;
  cursor: default;
}

.page.page--pc {
  display: none;
}

.previouspostslink {
  margin-right: rem(28);
  display: block;
  width: rem(38);
  height: rem(38);
  border: 1px solid $main;
  background-color: $base;
  transition: all .3s;
  position: relative;
}

.previouspostslink:hover {
  cursor: pointer;
  background-color: $main;
}

.previouspostslink::after {
  content: "";
  position: absolute;
  top: 34%;
  left: 42%;
  transform: translate(-50%, -50%);
  display: block;
  width: rem(12);
  height: rem(12);
  border-left: 1px solid $main;
  border-bottom: 1px solid $main;
  transform: rotate(45deg);
  transition: all .3s;
}

.previouspostslink:hover::after {
  border-left: 1px solid $base;
  border-bottom: 1px solid $base;
}

.nextpostslink {
  margin-left: rem(28);
  display: block;
  width: rem(38);
  height: rem(38);
  border: 1px solid $main;
  background-color: $base;
  transition: all .3s;
  position: relative;
}

.nextpostslink:hover {
  cursor: pointer;
  background-color: $main;
}

.nextpostslink::after {
  content: "";
  position: absolute;
  top: 34%;
  left: 24%;
  transform: translate(-50%, -50%);
  display: block;
  width: rem(12);
  height: rem(12);
  border-right: 1px solid $main;
  border-top: 1px solid $main;
  transform: rotate(45deg);
  transition: all .3s;
}

.nextpostslink:hover::after {
  border-right: 1px solid $base;
  border-top: 1px solid $base;
}




